import './App.scss';
import Header from './components/Header';
import {
	BrowserRouter as Router,
	Switch,
	Route,
	Link,
	Redirect,
	useParams,
	useRouteMatch
} from "react-router-dom";
import routes from "./shared/routes"


const App = () => {

	return (
		<Router>
			<Header />
				{routes.map((route, i) => (
					<Route
						key={i}
						exact={true}
						path={route.path}
						component={route.component}
						render={props => (
							<route.component {...props} routes={route.routes} />
						)}
					/>
				))}
		</Router>
	);
}

export default App;